import axios from 'axios';
import { EndpointConfig } from '../config/config';

export default class DriversRepository {
  // Definir el cliente Axios para consumir el servicio
  get ws() {
    const cfg = new EndpointConfig();
    const ws = axios.create({
      baseURL: cfg.URL_DEV,
      headers: cfg.headerToken(),
    });
    return ws;
  }

  async getAllDrivers() {
    const endpoint = '/drivers';
    const res = await this.ws.get(endpoint);
    return res.data;
  }

  async getDriverById(id) {
    const endpoint = `/drivers/${id}`;
    const res = await this.ws.get(endpoint);
    return res.data;
  }

  //   async update(id, body) {
  //     const endpoint = `/users/${id}`;

  //     let res = await this.ws.put(endpoint, body);
  //     return res;
  //   }
}
